.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.iconStyle {
  font-size: 18px;
  color: rgba(0, 0, 0, 0.65);
}
.analysis .titleNanme {
  position: relative;
  text-align: left;
  text-indent: 12px;
  font-weight: normal;
  font-size: 13px;
  margin-top: 5px;
}
.analysis .titleNanme::before {
  content: '';
  position: absolute;
  top: 1px;
  bottom: 3px;
  right: 0;
  left: 0;
  border-left: 3px solid var(--primary);
}
.analysis .titleNanme .iconBar {
  position: absolute;
  right: 10px;
  top: 0px;
  display: flex;
  font-size: 18px;
  color: rgba(0, 0, 0, 0.65);
}
.analysis .titleNanme .iconBar i {
  -webkit-transition: color 0.3s ease;
  transition: color 0.3s ease;
}
.analysis .titleNanme .iconBar i:hover {
  cursor: pointer;
  color: var(--primary);
}
.analysis .chartDom {
  height: 300px;
  width: 100%;
}
.analysis .controlHead {
  margin-bottom: 24px;
  overflow: auto;
}
.analysis .controlHead .ant-card-head {
  min-width: 580px;
}
.analysis .controlHead .headerContent {
  display: flex;
  align-items: center;
}
.analysis .controlHead .headerContent > i {
  font-size: 18px;
  color: rgba(0, 0, 0, 0.65);
  margin-right: 8px;
}
.analysis .controlHead .headerTextSpan {
  font-size: 13px;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.65);
  margin: 0 16px 0 8px;
}
.analysis .controlHead .headerTextSpan.rightSpace {
  font-size: 12px;
}
.analysis .controlHead .headerTextSpan.rightSpace i {
  margin-left: 8px;
  color: var(--primary);
  cursor: pointer;
}
.analysis .ant-select-selection-placeholder {
  font-size: 12px;
}
